.prodcut-details-container{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-start;
    gap: 10px;
    position: relative;
    padding: 20px 0px;
}
.left-details-co{
    width: 55%;
    height: 100%;
    position: sticky;
    top: 14%;
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.box-container-small{
    width: 100%;
    display: flex;
    align-items: center;
    gap: 10px;
    justify-content: center;
}
.small-box{
    width: 120px;
    height: 80px;
    border: 1px solid rgb(87, 83, 83);
    background: transparent;
    background-position: center !important;
    background-size: cover !important;

}
.main-product-box1{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 75vh;
}
.product-box2-details{
    width: 55%;
    height: 130vh;
    border: 1px solid black;
}
.product-heading{
    font-size: 50px;
    font-family: 'Rubik',sans-serif;
    text-align: center;
    border-bottom: 1px solid grey;
    background-color: rgb(34, 177, 212);
    color: rgb(0, 0, 0);
    position: relative;
}
.product-heading::after{
    position: absolute;
    content: '';
    width: 25%;
    top: 50%;
    right: 5%;
    height: 2px;
    background: rgb(0, 0, 0);
}
.product-heading::before{
    position: absolute;
    content: '';
    width: 25%;
    top: 50%;
    left: 5%;
    height: 2px;
    background: rgb(0, 0, 0);
}