.places-container {
  padding-top: 100px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 100px;
  overflow: hidden;
  z-index: -10;
  background-color: white;
}

.place-box-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 100%;
  gap: 70px;
}

.heading-section-places {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin: auto;
}

.popular-places-heading {
  font-family: 'Rubik', sans-serif;
  font-size: 40px;
  font-weight: bold;
}

.places {
  position: relative;
  z-index: 10;
}

.places::after {
  position: absolute;
  content: '';
  bottom: 7px;
  width: 100%;
  left: 0%;
  height: 6px;
  background-color: rgb(21 195 230);
  z-index: -9;
}

.description-about-places {
  width: 70%;
  text-align: center;
}

.description-about-places {
  font-family: 'Rubik', sans-serif;
  font-size: 17px;
  line-height: 25px;
  text-align: center;
}


/* box */
.showcase {
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}


.travel-card {
  position: relative;
  display: inline-flex;
  flex-flow: column nowrap;
  font-weight: 300;
  background-color: white;
  border: 1px solid rgb(233, 231, 231);
  border-radius: 15px;
  width: 350px;
  text-decoration: none;
  transition: all 0.3s ease;
  border-bottom: 5px solid rgb(21 195 230);
  border-radius: 20px;
  -webkit-tap-highlight-color: transparent;

}

.travel-card:hover>.image>img {
  transform: scale(1.2);
}


.travel-card .image {
  width: 100%;
  height: 250px;
  border-top-left-radius: inherit;
  border-top-right-radius: inherit;
  overflow: hidden;
}

.image>img {
  width: 100%;
  height: 100%;
  background-image: url('https://cache-graphicslib.viator.com/graphicslib/page-images/742x525/467829_Viator_Unsplash_170410.jpg');
  background-position: center;
  background-size: cover;
  border-top-left-radius: inherit;
  border-top-right-radius: inherit;
  transition: all .28s ease-in-out;
}

.travel-card>.content {
  color: #545454;
  padding: 1em 1.5em;
  transition: all 0.3s ease;
}

.content> :nth-child(1n+2) {
  margin-top: 1em;
}

.content>.category {
  font-size: 1.2em;
  color: #bababa;
}

.content>.topic {
  font-size: 1.3em;
  line-height: calc(1rem * 2.8);
  word-break: break-all;
  max-height: calc(1rem * 2.8 * 2);
  overflow: hidden;
  position: relative;
  background-color: white;
  font-family: 'Rubik', sans-serif;
  color: black;
}

.content>.recommendation {
  display: flex;
  align-items: center;
}

.recommendation>.score {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: rgb(254, 207, 139);
  background: linear-gradient(130deg, rgba(254, 207, 139, 1) 0%, rgba(255, 198, 115, 1) 75%, rgba(255, 187, 88, 1) 100%);
  color: white;
  padding: 5px 8px;
  border-radius: 30px;
  user-select: none;
}

.score> :first-child {
  padding-top: 1px;
  padding-left: 4px;
}

.score> :last-child {
  font-size: 1.1em;
  padding-left: 6px;
}


.recommendation>.comment {
  color: #bababa;
  margin-left: 1.5em;
}


.travel-card>.price {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Rubik', sans-serif;
}

.price>.discount-info {
  color: #4fc3f7;
}

.price>.original-price {
  font-size: 2em;
}


.viewMore-btn {
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Rubik', sans-serif;
  font-size: 18px;
  background-color: rgb(21 195 230);
  color: black;
  cursor: pointer;
  width: 200px;
  height: 50px;
  text-decoration: none;
  margin-bottom: 20px;
  border-radius: 10px;
  transition: all 0.3s ease;
}

.viewMore-btn:hover {
  background-color: rgb(21, 102, 200);
}



/* dot spinner */
.dot-container-spinner {
  display: flex;
  width: 100%;
  height: 70vh;
  align-items: center;
  justify-content: center;
}

.dot-spinner {
  --uib-size: 2.8rem;
  --uib-speed: .9s;
  --uib-color: rgb(21 195 230);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: var(--uib-size);
  width: var(--uib-size);
}

.dot-spinner__dot {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 100%;
  width: 100%;
}

.dot-spinner__dot::before {
  content: '';
  height: 20%;
  width: 20%;
  border-radius: 50%;
  background-color: var(--uib-color);
  transform: scale(0);
  opacity: 0.5;
  animation: pulse0112 calc(var(--uib-speed) * 1.111) ease-in-out infinite;
  box-shadow: 0 0 20px rgba(18, 31, 53, 0.3);
}

.dot-spinner__dot:nth-child(2) {
  transform: rotate(45deg);
}

.dot-spinner__dot:nth-child(2)::before {
  animation-delay: calc(var(--uib-speed) * -0.875);
}

.dot-spinner__dot:nth-child(3) {
  transform: rotate(90deg);
}

.dot-spinner__dot:nth-child(3)::before {
  animation-delay: calc(var(--uib-speed) * -0.75);
}

.dot-spinner__dot:nth-child(4) {
  transform: rotate(135deg);
}

.dot-spinner__dot:nth-child(4)::before {
  animation-delay: calc(var(--uib-speed) * -0.625);
}

.dot-spinner__dot:nth-child(5) {
  transform: rotate(180deg);
}

.dot-spinner__dot:nth-child(5)::before {
  animation-delay: calc(var(--uib-speed) * -0.5);
}

.dot-spinner__dot:nth-child(6) {
  transform: rotate(225deg);
}

.dot-spinner__dot:nth-child(6)::before {
  animation-delay: calc(var(--uib-speed) * -0.375);
}

.dot-spinner__dot:nth-child(7) {
  transform: rotate(270deg);
}

.dot-spinner__dot:nth-child(7)::before {
  animation-delay: calc(var(--uib-speed) * -0.25);
}

.dot-spinner__dot:nth-child(8) {
  transform: rotate(315deg);
}

.dot-spinner__dot:nth-child(8)::before {
  animation-delay: calc(var(--uib-speed) * -0.125);
}

@keyframes pulse0112 {

  0%,
  100% {
    transform: scale(0);
    opacity: 0.5;
  }

  50% {
    transform: scale(1);
    opacity: 1;
  }
}