.container-alert-popup {
    position: fixed;
    top: 0%;
    width: 100%;
    margin: auto;
    height: 100vh;
    z-index: 1054;
    overflow: hidden;
    padding-top: 10px;
}

::-webkit-scrollbar {
    width: 0px;
}

::-webkit-scrollbar-track {
    background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
    background: #888;
}

.container-message-box-aler {
    width: 50%;
    height: 50px;
    padding: 10px;
    box-shadow: 1px 1px 7px rgb(211, 209, 209);
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: auto;
}

.close-notification-alerticon {
    color: white;
    font-size: 20px;
    cursor: pointer;
}