.about-us-container-section{
    display: flex;
    width: 100%;
    height: 400px;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 30px;
    padding: 30px;
    margin-bottom: 150px;
}

.left-image-container-about-section{
    width: 500px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 500px;
}


.content-about{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
    gap: 30px;
    width: 50%;
    margin: auto;

}

#mission-about{
    font-size: 60px;
    font-family: 'Roboto',sans-serif;
    font-weight: 600;
}
#text-mission-about{
    font-size: 20px;
    font-family: 'Rubik',sans-serif;
    line-height: 28px;
}
#mission-about::first-letter{
    color: rgb(21 195 237) !important;
    font-size: 80px;
}